.container_imagens {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
}

.imagem_pokemon_evolução {
  max-width: 100%;
  max-height: 60%;
}

.imagem_seta {
  width: 5%;
}

.container_primeiro_pokemon,
.container_segundo_pokemon,
.container_terceiro_pokemon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.nome_poke_primeiro,
.nome_poke_segundo,
.nome_poke_terceiro {
    font-size: 90%;
}